@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  margin: auto;
  max-width: 768px;
  padding: 0px 20px;
}

.title {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.title-name {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}

.confirm {
  border: 1px solid #000000;
  background: #333333;
  border-radius: 4px;
  text-align: center;
  width: 100%;
  height: 50px;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}


.header {
  max-width: 768px;
  height: 60px;
  background: #333333;
  color: #ffffff;
  padding: 0 20px;
  margin: auto;
}
.header-box {
  width: 100%;
  height: 60px;
  background: #333333;
}

h1 {
  display: inline-block;
  margin-right: 15px;
}

.header-title {
  margin: 0 auto;
  max-width: 768px;
}

.box {
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 4px;
  height: 56px;
  width: 100%;
}

.input {
  font-size: 16px;
  font-style: normal;
  font-weight: bold;
  line-height: 19px;
  text-align: center;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
  outline: none;
}

.luck {
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #000000;
}

.email-title {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #000000;
}

.agree {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  color: #828282;
}

.hidden {
  display: none;
}

* {
  font-family: "Spoqa Han Sans", "Spoqa Han Sans JP", "Sans-serif";
}

