.container {
  margin: auto;
  max-width: 768px;
  padding: 0px 20px;
}

.title {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.title-name {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}

.confirm {
  border: 1px solid #000000;
  background: #333333;
  border-radius: 4px;
  text-align: center;
  width: 100%;
  height: 50px;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
