.box {
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 4px;
  height: 56px;
  width: 100%;
}

.input {
  font-size: 16px;
  font-style: normal;
  font-weight: bold;
  line-height: 19px;
  text-align: center;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
  outline: none;
}

.luck {
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #000000;
}

.email-title {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #000000;
}

.agree {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  color: #828282;
}

.hidden {
  display: none;
}
